.card {
  background-image: url("../../images/country-card.jpg");
  width: 200px;
  height: 200px;
  border-radius: 10px;
  padding: 10px 10px;
  box-sizing: border-box;
  margin: 0 20px;
  color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  margin-bottom: 10px;
  cursor: pointer;
}

.countryName {
  font-size: 30px;
  font-family: "Roboto", sans-serif;
  display: flex;
  justify-content: center;
  text-align: center;
  height: 110px;
}

.stat {
  margin-top: 10px;
}

.countryRecovered {
  text-align: center;
  font-size: 20px;
  color: #6af8aa;
}

.statContainer {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.countryCases {
  font-size: 20px;
  color: #2e2eb4;
  display: flex;
  justify-content: center;
}

.countryDeaths {
  font-size: 20px;
  color: #fb9292;
}

.favoritePanel {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.favoritePanel img {
  cursor: pointer;
}
