.container {
  width: 300px;
  height: 400px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  margin: 20px;
  box-sizing: border-box;
  overflow: hidden;
}

.image {
  height: 180px;
}

.title {
  height: 75px;
}

.content {
  padding: 18px;
  overflow: hidden;
  font-family: "Roboto", sans-serif;
  color: rgb(27, 26, 26);
}

.description {
  margin-top: 10px;
  height: 80px;
  font-size: 14px;
  color: rgb(131, 124, 124);
}
.image img {
  width: 300px;
  height: 180px;
}

.source {
  margin-top: 10px;
  font-size: 14px;
  color: rgb(131, 124, 124);
}

a {
  text-decoration: none;
}
