.newsContainer {
  width: 80%;
  min-height: 600px;
  display: flex;
  justify-content: center;

  background-color: #fff;
  border-radius: 20px;
  flex-wrap: wrap;
  padding: 20px;
  box-sizing: border-box;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  margin: 0 auto;
  margin-top: 100px;
}

.container {
  width: 100%;
  margin-top: -500px;
}
